// HTML GLOBAL
html {
    font-size: 62.5% !important;
}

$body-bg: #fff;
$body-color: #141414;
$font-size-base: 1.4rem;
// OTHER
$transition : 0.5s;
//  FONT WEIGHT
$font-weight-light: 300;
$font-weight-normal: 400;
$font-weight-medium: 500;
$font-weight-semibold: 600;
$font-weight-bold: 700;

// COLOR
:root {
    // color default
    --white: #fff;
    --gray-100: #f8f9fa;
    --gray-200: #e9ecef;
    --gray-300: #dee2e6;
    --gray-400: #ced4da;
    --gray-500: #D9D9D9;
    --gray-600: #8c8c8c;
    --gray-700: #595959;
    --gray-800: #454545;
    --gray-900: #141414;
    --black: #000;
    // color site
    --primary: #00A859;
    --secondary: #ED342E;
    --title: #141414;
    --summary: #595959;
    --border-color: #D9D9D9;
    --hover: #00A859;
}

// FONT 
:root {
    --font-family-sans-serif: "Lexend", Arial, Helvetica, sans-serif;
    // --font-family-serif: "Noto Serif", Arial, Helvetica, sans-serif;
}

// GUTTER
// :root {
