// BREAKPPOINT
$grid-breakpoints: () !default;
$grid-breakpoints: map-merge((
            sm: 576px,
            md: 768px,
            lg: 1024px,
            xl: 1366px,
        ),
        $grid-breakpoints);
@import "mixins/breakpoints";

:root {

    // 0-1023
    @include media-breakpoint-down(md) {
        --container: 730px;
    }

    // 1024-1279
    @include media-breakpoint-between(lg, lg) {
        --container: 980px;
    }

    //1280
    @include media-breakpoint-up(xl) {
        --container: 1170px;
    }
}